export default {
  name: "navbar",
  data: () => ({
    connected: false,
    notConnected: true
  }),
  props: ['page'],
  methods: {
    logout() {
      if (process.browser) {
        localStorage.removeItem('jwt');
        localStorage.removeItem('username');
        window.location = "/"
      }
    },
  },
  created() {
    if (process.browser) {
      if (localStorage.getItem('jwt') != null) {
        this.connected = true
        this.notConnected = false
      }
    }
  }
}
